import React, { useState, useEffect } from "react";
import TutorialDataService from "../services/Tutorialservice";
import { Link } from "react-router-dom";
import Pagination from "@material-ui/lab/Pagination";
import Loading from "./Loading";
import { useCookies } from "react-cookie";

import FetchError from "./FetchError";

const TutorialList = () => {
  const [tutorials, setTutorials] = useState([]);
  const [currentTutorial, setCurrentTutorial] = useState(null);
  const [currentIndex, setCurrentIndex] = useState(-1);
  const [searchTitle, setSearchTitle] = useState("");
  const [loading, setLoading] = useState(false);
  const [fetcherror, setFetchError] = useState(false);
  const [token, setToken] = useCookies(["mytoken"]);

  const onChangeSearchTitle = (event) => {
    const searchTitle = event.target.value;
    setSearchTitle(searchTitle);
  };

  const retrieveTutorials = async () => {
    setLoading(true);
    try {
      const response = await TutorialDataService.getAll(token["mytoken"]);
      setTutorials(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      setFetchError(true);
    }
  };

  useEffect(() => {
    retrieveTutorials();
  }, []);

  const refreshList = () => {
    retrieveTutorials();
    setCurrentTutorial(null);
    setCurrentIndex(-1);
  };

  const activeTutorial = (tutorials, index) => {
    setCurrentIndex(index);
    setCurrentTutorial(tutorials);
  };

  const removeAllTutorials = () => {
    TutorialDataService.removeAll()
      .then((response) => {
        // console.log(response.data);
        refreshList();
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const findByTitle = () => {
    setLoading(true);
    TutorialDataService.findByTitle(searchTitle, token["mytoken"])
      .then((response) => {
        setTutorials(response.data);
        console.log(response.data);
        setLoading(false);
      })
      .catch((error) => {
        setLoading(false);
        setFetchError(true);
      });
  };

  if (loading) {
    return <Loading />;
  }
  if (fetcherror) {
    return <FetchError />;
  }

  return (
    <div className="list row">
      <div className="col-md-8">
        <div className="input-group mb-3">
          <input
            type="text"
            className="form-control"
            placeholder="Search by title"
            value={searchTitle}
            onChange={onChangeSearchTitle}
          />
          <div className="input-group-append">
            <button
              className="btn btn-outline-secondary"
              type="button"
              onClick={findByTitle}
            >
              Search
            </button>
          </div>
        </div>
      </div>

      <div className="col-md-6">
        <h4>Tutorials List</h4>

        <ul className="list-group">
          {tutorials &&
            tutorials.map((tutorial, index) => (
              <li
                className={
                  "list-group-item " + (index === currentIndex ? "active" : "")
                }
                onClick={() => activeTutorial(tutorial, index)}
                key={index}
              >
                {tutorial.name}
              </li>
            ))}
        </ul>

        {/* <button
          className="m-3 btn btn-sm btn-danger"
          onClick={removeAllTutorials}
        >
          Remove All
        </button> */}
      </div>

      <div className="col-md-6">
        {currentTutorial ? (
          <div>
            <h4>Vetofficer</h4>
            <div>
              <label>
                <strong>Title:</strong>
              </label>{" "}
              {currentTutorial.name}
            </div>
            <div>
              <label>
                <strong>Description:</strong>
              </label>{" "}
              {currentTutorial.county}
            </div>
            <div>
              <label>
                <strong>Status:</strong>
              </label>{" "}
              {currentTutorial.published ? "Active" : "Deactivated"}
            </div>

            <Link
              to={"/tutorials/" + currentTutorial.id}
              className="btn badge-primary"
            >
              Edit
            </Link>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Tutorial...</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default TutorialList;
