import React from "react";
import styled from "styled-components";

function Loading() {
  return (
    <Wrapper className="container">
      <div>
        <h1>Loading...</h1>
      </div>
    </Wrapper>
  );
}

const Wrapper = styled.main`
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
`;
export default Loading;
