import http from "../http-common";

const getAll = (token) => {
  return http.get(`api/vetofficers/`, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const get = (id, token) => {
  return http.get(`api/vetofficers/${id}/`, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const create = (data, token) => {
  return http.post("api/vetofficers/", data, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const update = (id, data, token) => {
  return http.put(`api/vetofficers/${id}/`, data, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const remove = (id, token) => {
  return http.delete(`api/vetofficers/${id}/`, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const removeAll = () => {
  return http.delete(`api/vetofficers/`);
};

const findByTitle = (name, token) => {
  return http.get(`api/vetofficers?name=${name}`, {
    headers: {
      Authorization: `Token ${token}`,
    },
  });
};

const loginUser = (body) => {
  return http.post(`auth/`, body);
};

const TutorialDataService = {
  getAll,
  get,
  loginUser,
  create,
  update,
  remove,
  removeAll,
  findByTitle,
};
export default TutorialDataService;
