import React, { useState, useEffect } from "react";
import TutorialDataService from "../services/Tutorialservice";
import Loading from "./Loading";
import FetchError from "./FetchError";
import SubmitError from "./SubmitError";
import { useCookies } from "react-cookie";

const Tutorial = (props) => {
  const initialTutorialState = {
    id: null,
    name: "",
    county: "",
    id_no: "",
    phone_no: "",
    email: "",
    published: false,
  };

  const [currentTutorial, setCurrentTutorial] = useState(initialTutorialState);
  const [message, setMessage] = useState("");
  const [loading, setLoading] = useState(false);
  const [fetcherror, setFetchError] = useState(false);
  const [push, setPush] = useState(false);
  const [token, setToken] = useCookies(["mytoken"]);

  const getTutorial = async (id) => {
    setLoading(true);
    try {
      const response = await TutorialDataService.get(id, token["mytoken"]);
      setCurrentTutorial(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      console.log(error);
      setFetchError(true);
    }
  };

  useEffect(() => {
    getTutorial(props.match.params.id);
  }, [props.match.params.id]);

  const handleInputChange = (event) => {
    const { value, name } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
  };

  const updatePublished = async (status) => {
    let data = {
      id: currentTutorial.id,
      name: currentTutorial.name,
      county: currentTutorial.county,
      id_no: currentTutorial.id_no,
      phone_no: currentTutorial.phone_no,
      email: currentTutorial.email,
      published: status,
    };
    try {
      const response = await TutorialDataService.update(
        currentTutorial.id,
        data,
        token["mytoken"]
      );
      setCurrentTutorial({ ...currentTutorial, published: status });
    } catch (error) {
      setPush(true);
    }
  };

  const updateTutorial = () => {
    TutorialDataService.update(
      currentTutorial.id,
      currentTutorial,
      token["mytoken"]
    )
      .then((response) => {
        setMessage("The officer was updated successfully!");
      })
      .catch((e) => {
        setPush(true);
      });
  };

  const deleteTutorial = () => {
    TutorialDataService.remove(currentTutorial.id, token["mytoken"])
      .then((response) => {
        props.history.push("/tutorials/");
      })
      .catch((e) => {
        setPush(true);
      });
  };

  if (loading) {
    return <Loading />;
  }
  if (fetcherror) {
    return <FetchError />;
  }
  if (push) {
    return <SubmitError />;
  }

  return (
    <div>
      {currentTutorial ? (
        <div className="edit-form">
          <h4>VetOfficer</h4>
          <form>
            <div className="form-group">
              <label htmlFor="title">Name</label>
              <input
                type="text"
                className="form-control"
                id="name"
                name="name"
                value={currentTutorial.name}
                required
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="county">County</label>
              <input
                type="text"
                className="form-control"
                id="county"
                name="county"
                value={currentTutorial.county}
                required
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="phone_no">Phone no</label>
              <input
                type="number"
                className="form-control"
                id="phone_no"
                name="phone_no"
                minLength="10"
                value={currentTutorial.phone_no}
                required
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="phone_no">ID no</label>
              <input
                type="number"
                className="form-control"
                id="id_no"
                name="id_no"
                value={currentTutorial.id_no}
                required
                minLength="8"
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label htmlFor="phone_no">Email</label>
              <input
                type="email"
                className="form-control"
                id="email"
                name="email"
                value={currentTutorial.email}
                required
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <label>
                <strong>Status:</strong>
              </label>
              {currentTutorial.published ? "Active" : "Deactivated"}
            </div>
          </form>

          {currentTutorial.published ? (
            <button
              className="btn btn-primary mr-2"
              onClick={() => updatePublished(false)}
            >
              Deactivate
            </button>
          ) : (
            <button
              className="btn btn-primary mr-2"
              onClick={() => updatePublished(true)}
            >
              Activate
            </button>
          )}

          <button className="btn btn-danger mr-2" onClick={deleteTutorial}>
            Delete
          </button>

          <button
            type="submit"
            className="btn btn-success"
            onClick={updateTutorial}
          >
            Update
          </button>
          <p>{message}</p>
        </div>
      ) : (
        <div>
          <br />
          <p>Please click on a Tutorial...</p>
        </div>
      )}
    </div>
  );
};

export default Tutorial;
