import React, { useState } from "react";
import TutorialDataService from "../services/Tutorialservice";
import SubmitError from "./SubmitError";
import { useCookies } from "react-cookie";

const AddTutorial = () => {
  const initialTutorialState = {
    id: null,
    name: "",
    county: "",
    id_no: "",
    phone_no: "",
    email: "",
    published: false,
  };

  const [tutorial, setTutorial] = useState(initialTutorialState);
  const [submitted, setSubmitted] = useState(false);
  const [save, setSave] = useState(false);
  const [token, setToken] = useCookies(["mytoken"]);

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setTutorial({ ...tutorial, [name]: value });
  };

  const saveTutorial = () => {
    let data = {
      name: tutorial.name,
      county: tutorial.county,
      id_no: tutorial.id_no,
      phone_no: tutorial.phone_no,
      email: tutorial.email,
    };
    TutorialDataService.create(data, token["mytoken"])
      .then((response) => {
        setTutorial({
          id: response.data.id,
          name: response.data.name,
          county: response.data.county,
          id_no: response.data.id_no,
          phone_no: response.data.phone_no,
          email: response.data.email,
          published: response.data.published,
        });
        setSubmitted(true);
        // console.log(response.data);
      })
      .catch((error) => {
        setSave(true);
      });
  };

  const newTutorial = () => {
    setTutorial(initialTutorialState);
    setSubmitted(false);
  };

  if (save) {
    return <SubmitError />;
  }

  return (
    <div className="submit-form">
      {submitted ? (
        <div>
          <h4>You submitted successfully!</h4>
          <button className="btn btn-success" onClick={newTutorial}>
            Add
          </button>
        </div>
      ) : (
        <div>
          <div className="form-group">
            <label htmlFor="name">Name</label>
            <input
              type="text"
              className="form-control"
              id="name"
              required
              value={tutorial.name}
              onChange={handleInputChange}
              name="name"
            />
          </div>

          <div className="form-group">
            <label htmlFor="name">County</label>
            <input
              type="text"
              className="form-control"
              id="county"
              required
              value={tutorial.county}
              onChange={handleInputChange}
              name="county"
            />
          </div>

          <div className="form-group">
            <label htmlFor="name">Email</label>
            <input
              type="email"
              className="form-control"
              id="email"
              required
              value={tutorial.email}
              onChange={handleInputChange}
              name="email"
            />
          </div>

          <div className="form-group">
            <label htmlFor="name">Phone no</label>
            <input
              type="number"
              className="form-control"
              id="phone_no"
              required
              value={tutorial.phone_no}
              onChange={handleInputChange}
              name="phone_no"
            />
          </div>

          <div className="form-group">
            <label htmlFor="id_no">Id no</label>
            <input
              type="number"
              className="form-control"
              id="id_no"
              required
              value={tutorial.id_no}
              onChange={handleInputChange}
              name="id_no"
            />
          </div>

          <button onClick={saveTutorial} className="btn btn-success">
            Submit
          </button>
        </div>
      )}
    </div>
  );
};

export default AddTutorial;
